.ac .ac-trigger {
  color: #111;
  text-align: left;
  width: 100%;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  margin: 0;
  padding: 8px 32px 8px 8px;
  font: bold 16px Arial, sans-serif;
  text-decoration: none;
  transition: color .25s;
  display: block;
  position: relative;
}

.ac .ac-trigger:after {
  content: "+";
  text-align: center;
  width: 15px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(0, -50%);
}

.ac .ac-trigger:focus {
  color: #8a8a8a;
}

.ac .ac-panel {
  transition-property: height, visibility;
  transition-timing-function: ease;
  overflow: hidden;
}

.ac .ac-panel .ac-text {
  color: #111;
  margin: 0;
  padding: 8px;
  font: 15px / 24px Arial, sans-serif;
}

.ac.js-enabled .ac-panel {
  visibility: hidden;
}

.ac.is-active .ac-panel {
  visibility: visible;
}

.ac.is-active > .ac-header .ac-trigger:after {
  content: "–";
}

